import { Component, OnInit } from '@angular/core';
import {Individual} from "./individual";
import {IndividualsService} from "./individuals.service";
import {PageRequest} from "../domain/page-request";
import {Subscription} from "rxjs";

@Component({
  selector: 'app-individuals',
  templateUrl: './individuals.component.html',
  styleUrls: ['./individuals.component.css']
})
export class IndividualsComponent implements OnInit {

  individuals: Individual[];
  subscription: Subscription;

  constructor(private service: IndividualsService) {}

  ngOnInit() {
    this.subscription = this.service.search(PageRequest.default('individuals')).subscribe(
        response => {
            this.individuals = response.entityList();
        },
        err => {

        },
        () => {
            this.subscription.unsubscribe();
        }
    )
  }

}
